
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.ug-marqueetext{white-space:nowrap;overflow:hidden
}
.ug-marqueetext>div{display:inline-block;-webkit-animation:marquee linear infinite;animation:marquee linear infinite
}
.ug-marqueetext .pause{-webkit-animation-play-state:paused;animation-play-state:paused
}
.ug-marqueetext .running{-webkit-animation-play-state:running;animation-play-state:running
}
